<template>
  <div id="container">
    <HeaderUserBar />

    <div id="my-tickets">
      <div class="mt-title">Мои тикеты</div>
      <div class="mt-list" v-if="$store.state.tickets_count">
        <div class="ticket-id " v-for="ticket in $store.state.tickets"><span :class="ticket.status">№ {{ ticket.code }}</span></div>
        <div class="ticket-id " v-for="ticket in $store.state.tickets"><span :class="ticket.status">№ {{ ticket.code }}</span></div>
      </div>
      <div class="mt-no-ticket" v-else>Тикетоа пока нет!</div>
    </div>
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
export default {
  name: "MyTickets",
  components: {
    HeaderUserBar
  },
  async mounted() {
    Telegram.WebApp.BackButton.show()
    Telegram.WebApp.BackButton.onClick(() => this.$router.push('/profile'));

    await this.$store.dispatch('getTickets', Telegram.WebApp.initDataUnsafe.user.id)

  }
}
</script>
<style scoped>
@import url('../style/my-tickets.css');
</style>
